* {
    margin:0;
    padding: 0;
}

html, body {
    overflow: hidden;
    justify-content: center;
    align-items: center;
}

canvas{
    background-image: radial-gradient(circle, rgb(242, 203, 171), rgb(242, 134, 0));
}

.webgl {
    position: fixed;
    top:0;
    left: 0;
    outline: none;
}

.loading-bar {
    position: absolute;
    top: 50%;
    width: 100%;
    height:2px;
    background: white;
    transform: scaleX(0);
    transition: transform 0.5s;
    will-change: transform;
}

.loading-bar.ended {
    transition: transform 1.5s ease-in-out;
}

.loading-text{
    position: absolute;
    top: 47%;
    left: 50%;
    transform: translate(-50%, -50% );
    color: white;
    transform: scaleX(0);
    transition: transform 1s;
    will-change: transform;
}

.center{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.reticle{
    display: flex;
    border: 3px solid;
    position: absolute;
    aspect-ratio: 1/1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50% );
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: rgb(139, 139, 139);
    border-color: white;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 4px 2px black;
}

.inner-reticle{
    aspect-ratio: 1/1;
    width: 4px;
    height: 4px;
    background-color: white;
    margin: auto;
    border-radius: 50%;
}

.reticle.hidden{
    display: none;
}

.cart{
    position: absolute;
    display: flex;
    top: 50px;
    right: 50px;
    width: 50px;
    height: 50px;
    border: 2px solid;
    border-radius: 5px;
    border-color: beige;
    background-color: grey;
    z-index: 10;
    cursor: pointer;
}

.cart-icon{
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 40px;
    height: 40px;
    color: beige;
}
.cart:hover{
    background-color: beige;
    border-color: grey;
}

.cart-icon:hover{
    color: grey;
}

.cart-count{
    position: relative;
    display: flex;
    text-align: center;
    justify-content: center;
    top: 4px;
    left: -1px;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    background-color: red;
}
.cart-number{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin: auto;
    color: white;
    font-weight: bold;
}

.cart-content{
    position: absolute;
    z-index: 11;
    width: 400px;
    top: 50px;
    right: 50px;
    background-color: aliceblue;
}